* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #111;
  font-family: Poppins, sans-serif;
  font-weight: 500;
}

.content {
  height: 100vh;
  align-items: center;
  padding-left: 45vw;
  display: flex;
}

@media screen and (max-width: 1600px) {
  .content {
    padding-left: 40vw;
  }
}

@media screen and (max-width: 1399px) {
  .content {
    padding-left: 25vw;
    position: relative;
  }
}

@media screen and (max-width: 1023px) {
  .content {
    padding: 0 20px;
  }
}

.content__title {
  color: #fff;
  font-size: 80px;
  font-weight: 800;
  line-height: 100px;
}

@media screen and (max-width: 1399px) {
  .content__title {
    font-size: 70px;
    line-height: 90px;
  }
}

@media screen and (max-width: 1023px) {
  .content__title {
    font-size: 60px;
    line-height: 80px;
  }
}

@media screen and (max-width: 767px) {
  .content__title {
    font-size: 32px;
    line-height: 48px;
  }
}

.content__descr {
  color: #838383;
  margin-top: 20px;
  font-size: 20px;
  line-height: 30px;
}

@media screen and (max-width: 767px) {
  .content__descr {
    font-size: 16px;
    line-height: 24px;
  }
}

.content__actions {
  gap: 50px;
  margin-top: 60px;
  display: flex;
}

@media screen and (max-width: 1023px) {
  .content__actions {
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
  }
}

@media screen and (max-width: 767px) {
  .content__actions {
    margin-top: 40px;
  }
}

.subscribe-form {
  background-color: #292929;
  border-radius: 14px;
  padding: 8px;
  display: flex;
}

.subscribe-form__input {
  width: 260px;
  color: #fff;
  background-color: #0000;
  border: none;
  outline: none;
  padding: 0 12px;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}

.subscribe-form__input::placeholder {
  color: #838383;
}

@media screen and (max-width: 767px) {
  .subscribe-form__input {
    width: 170px;
  }
}

.subscribe-form__btn {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #fc8f2b;
  border: none;
  border-radius: 8px;
  padding: 12px 30px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
}

@media screen and (max-width: 767px) {
  .subscribe-form__btn {
    padding: 12px 20px;
  }
}

.socials {
  gap: 10px;
  list-style: none;
  display: flex;
}

.socials__item {
  width: 60px;
  height: 60px;
  background-color: #292929;
  border-radius: 10px;
}

.socials__link {
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.boxes {
  height: 100vh;
  gap: 60px;
  padding: 60px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .boxes {
    width: 100%;
    gap: 20px;
    padding: 20px;
  }
}

.boxes__logo {
  aspect-ratio: 1 / 1;
  z-index: 1;
  background-color: #111;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 38px 6px #fff;
}

@media screen and (max-width: 1399px) {
  .boxes__logo {
    display: none;
  }
}

.boxes__row {
  height: 200%;
  flex-direction: column;
  gap: 20px;
  animation-name: marquee-boxes-up;
  animation-duration: 15s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  display: flex;
}

@media screen and (max-width: 767px) {
  .boxes__row {
    flex: 1;
  }
}

.boxes__row-inner {
  height: 50%;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.boxes__item {
  height: calc(20% - 16px);
  object-fit: contain;
  opacity: .3;
  transition: opacity .3s;
}

.boxes__item:hover {
  opacity: 1;
}

@keyframes marquee-boxes-down {
  from {
    transform: translateY(calc(-50% - 20px));
  }

  to {
    transform: translateY(0%);
  }
}

@keyframes marquee-boxes-up {
  from {
    transform: translateY(0%);
  }

  to {
    transform: translateY(calc(-50% - 20px));
  }
}

