* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #111111;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
}

.content {
  height: 100vh;
  padding-left: 45vw;
  display: flex;
  align-items: center;

  @media screen and (max-width: 1600px){
    padding-left: 40vw;
  }

  @media screen and (max-width: 1399px){
    position: relative;
    padding-left: 25vw;
  }

  @media screen and (max-width: 1023px){
    padding: 0 20px;
  }

  &__title {
    font-weight: 800;
    font-size: 80px;
    line-height: 100px;
    color: #ffffff;

    @media screen and (max-width: 1399px){
      font-size: 70px;
      line-height: 90px;
    }

    @media screen and (max-width: 1023px){
      font-size: 60px;
      line-height: 80px;
    }

    @media screen and (max-width: 767px){
      font-size: 32px;
      line-height: 48px;
    }
  }

  &__descr {
    margin-top: 20px;
    font-size: 20px;
    line-height: 30px;
    color: #838383;

    @media screen and (max-width: 767px){
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__actions {
    display: flex;
    gap: 50px;
    margin-top: 60px;

    @media screen and (max-width: 1023px){
      flex-direction: column;
      align-items: flex-start;
      gap: 30px;
    }

    @media screen and (max-width: 767px){
      margin-top: 40px;
    }
  }
}

.subscribe-form {
  display: flex;
  padding: 8px;
  background-color: #292929;
  border-radius: 14px;

  &__input {
    width: 260px;
    padding: 0 12px;
    background-color: transparent;
    border: none;
    outline: none;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #ffffff;

    &::placeholder {
      color: #838383;
    }

    @media screen and (max-width: 767px){
      width: 170px;
    }
  }

  &__btn {
    padding: 12px 30px;
    background-color: #FC8F2B;
    border: none;
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    text-transform: uppercase;
    cursor: pointer;

    @media screen and (max-width: 767px){
      padding: 12px 20px;
    }
  }
}

.socials {
  display: flex;
  gap: 10px;
  list-style: none;

  &__item {
    width: 60px;
    height: 60px;
    background-color: #292929;
    border-radius: 10px;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
  }
}

.boxes {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  gap: 60px;
  height: 100vh;
  padding: 60px;
  overflow: hidden;

  @media screen and (max-width: 767px){
    width: 100%;
    gap: 20px;
    padding: 20px;
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    aspect-ratio: 1 / 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #111111;
    box-shadow: 0 0 38px 6px #fff;
    border-radius: 10px;
    z-index: 1;

    @media screen and (max-width: 1399px){
      display: none;
    }
  }

  &__row {
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 200%;
    animation-name: marquee-boxes-up;
    animation-duration: 15s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    //&:nth-child(2) {
    //  animation-name: marquee-boxes-down;
    //  animation-duration: 15s;
    //}

    @media screen and (max-width: 767px){
      flex: 1;
    }
  }

  &__row-inner {
    height: 50%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__item {
    height: calc((100% - 80px) / 5);
    object-fit: contain;
    opacity: 0.3;
    transition: opacity 0.3s;

    &:hover {
      opacity: 1;
    }
  }
}

@keyframes marquee-boxes-down {
  from {
    transform: translateY(calc(-50% - 20px))

  }
  to {
    transform: translateY(0%);
  }
}

@keyframes marquee-boxes-up {
  from {
    transform: translateY(0%);
  }
  to {
    transform: translateY(calc(-50% - 20px))
  }
}